import { FC, useState } from 'react';
import './styled_News.scss'
import { useTypesSelector } from "../../../../hooks/useTypedSelector";
import { Button } from '@mui/material';
import ItemLastNew from '../../../../customeComponents/itemLastNew/ItemLastNew';
import { useTranslation } from 'react-i18next';

const News: FC = () => {
    const { loading, error, news } = useTypesSelector((state) => state.news);
    const [amountOfNews, setAmountOfNews] = useState(10);
    const [buttonState, setButtonState] = useState(false);
    const { t } = useTranslation();

    const handleClickOnButton = () => {
        if (news.length > amountOfNews) {
            setAmountOfNews(amountOfNews + 10);
        } else {
            setButtonState(true);
        }
    }

    if (loading) {
        return <h2>{t('loadingNewsPage')}</h2>;
    }
    if (error) {
        return <h2>{t('errorNewsPage')}</h2>;
    }

    return (
        <div className='wrapperNews'>
            {news.slice(0, amountOfNews).map((newsItem) => (
                <ItemLastNew key={newsItem.id} newsItem={newsItem} />
            ))}
            <Button disabled={buttonState} onClick={handleClickOnButton}>
                {t('loadMore')}
            </Button>
        </div>
    );
};

export default News;