interface newsItemType {
    id: number;
    author: string;
    title: string;
    description: string;
    url: string;
    urlToImage: string;
    publishedAt: string;
}

export const filterNews = (newsItems: any[]) => {
    const filteredNewsItems: any[] = [];
    newsItems.map((newsItem: newsItemType, index: number) => {
        filteredNewsItems.push({
            id: index,
            author: newsItem.author ? newsItem.author : 'unknown',
            title: newsItem.title,
            description: newsItem.description,
            url: newsItem.url,
            urlToImage: newsItem.urlToImage,
            publishedAt: newsItem.publishedAt,
        })
    })
    return filteredNewsItems
}