export interface NewsState {
    news: any[];
    loading: boolean;
    error: boolean;
}

export enum NewsActionsTypes {
    FETCH_NEWS = 'FETCH_NEWS',
    FETCH_NEWS_ERROR = 'FETCH_NEWS_ERROR',
    FETCH_NEWS_SUCCESS = 'FETCH_NEWS_SUCCESS',
    DELETE_ITEM_NEWS = 'DELETE_ITEM_NEWS',
}

interface FetchNewsAction {
    type: NewsActionsTypes.FETCH_NEWS;
}
interface FetchNewsErrorAction {
    type: NewsActionsTypes.FETCH_NEWS_ERROR;
    payload: string;
}
interface FetchNewsSuccessAction {
    type: NewsActionsTypes.FETCH_NEWS_SUCCESS;
    payload: any[];
}
interface DeleteItemNewsAction {
    type: NewsActionsTypes.DELETE_ITEM_NEWS;
    payload: any[];
}

export type NewsAction =
    FetchNewsAction
    | FetchNewsErrorAction
    | FetchNewsSuccessAction
    | DeleteItemNewsAction