import Button from '@mui/material/Button';
import { FC } from "react";
import DeleteIcon from '@mui/icons-material/Delete';
import "./styles_ItemLastNew.scss";
import { useTranslation } from "react-i18next";
import { useTypesSelector } from '../../hooks/useTypedSelector';
import { useActions } from '../../hooks/useActions';

interface ItemNewsProps {
    id: number;
    url: string;
    title: string;
    author: string;
    urlToImage: string;
    publishedAt: string;
    description: string;
}
interface ItemNews {
    newsItem: ItemNewsProps
}


const ItemLastNew: FC<ItemNews> = ({newsItem}) => {
    const { news } = useTypesSelector((state) => state.news);
    const { deleteNews } = useActions();
    const { t } = useTranslation();
    
    return (
        <div className="wrapperItemLastNew">
            <div
                className="wrapperItemLastNew_img"
                style={{ backgroundImage: `url(${newsItem.urlToImage})` }}
            />
            <div className="wrapperItemLastNew_content">
                <div className="wrapperItemLastNew_content_header">
                    <h3 className="wrapperItemLastNew_content_headerTitle">
                        {newsItem.title}
                    </h3>
                    <p className="wrapperItemLastNew_content_author">
                        <span>{t('author')} - </span>
                        {newsItem.author}
                    </p>
                </div>
                <p className="wrapperItemLastNew_content_description">
                    <span>{t('description')} - </span>
                    {newsItem.description}
                </p>
                <p className="wrapperItemLastNew_content_time">
                    <span>{t('time')} - </span>
                    {new Date(Date.parse(newsItem.publishedAt)).toLocaleString()}
                </p>
                <Button
                    size='small'
                    variant="contained"
                    onClick={() => window.open(newsItem.url, "_blank")}
                >
                    {t('read')}
                </Button>
            </div>
            <DeleteIcon onClick={() => deleteNews(news, newsItem.id)}/>
        </div>
    );
};

export default ItemLastNew;
