import axios from "axios"
import { Dispatch } from "redux"
import { filterNews } from "../../logic/logic"
import { NewsAction, NewsActionsTypes } from "../../types/news"

export const fetchNews = () => {
    return async (dispatch: Dispatch<NewsAction>) => {
        try {
            dispatch({type: NewsActionsTypes.FETCH_NEWS})
            const response = await axios.get('https://saurav.tech/NewsAPI/top-headlines/category/health/in.json')
            dispatch({type: NewsActionsTypes.FETCH_NEWS_SUCCESS, payload: filterNews(response.data.articles)})
        } catch (error) {
            dispatch({
                type: NewsActionsTypes.FETCH_NEWS_ERROR,
                payload: 'Произошла ошибка загрузки новостей'
            })
        }
    }
}

export const deleteNews = (newsItems: any[], deleteNewsId: number) => {
    const filteredNewsItemsAfterDelete: any[] = newsItems.filter((newsItem) => {
        return newsItem.id !== deleteNewsId
    })
    
    return({type: NewsActionsTypes.DELETE_ITEM_NEWS, payload: filteredNewsItemsAfterDelete})
}