import { Button } from "@mui/material";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import './styles_header.scss';

const Header = () => {
    const authorizationInfo = localStorage.getItem("authorization");
    const { t } = useTranslation();

    return (
        <div className="wrapperHeader">
            <div className="wrapperHeader_buttonContainer">
                <Link to="/">
                    <Button variant="contained">{t("main")}</Button>
                </Link>
                <Link to="/news">
                    <Button variant="contained">{t("news")}</Button>
                </Link>
            </div>
            {authorizationInfo
                ? <Link to="/profile">
                    <Button variant="contained">{t("profile")}</Button>
                  </Link>
                : <Link to="/logIn">
                    <Button variant="contained">{t("logIn")}</Button>
                  </Link>
            }
        </div>
    );
};

export default Header;
