import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import React, { FC, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import "./styles_logIn.scss";

const LogIn: FC = () => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const [errorLogIn, setErrorLogIn] = useState("");
    const logInState = {password: '12345', userName: 'admin'};
    const { t } = useTranslation();

    const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        if (logInState.password === password && logInState.userName === username) {
            const authorizationInfo = JSON.stringify(true);
            localStorage.setItem("authorization", authorizationInfo);
            setErrorLogIn('');
            window.location.replace("/profile")
        } else {
            setErrorLogIn('logInError')
        }
        
    };

    useEffect(() => {
        setErrorLogIn('');
    }, [])

    return (
        <form className="wrapperLogIn" onSubmit={handleSubmit}>
            <p>
                {t(errorLogIn)}
            </p>
            <div>
                <TextField
                    id="outlined-basic"
                    value={username}
                    label="Login"
                    variant="outlined"
                    onChange={(event) => setUsername(event.target.value)}
                    className="wrapperLogIn_userName"
                    margin="normal"
                    error={!!errorLogIn}
                />
            </div>
            <div>
                <TextField
                    id="outlined-adornment-password"
                    type="password"
                    value={password}
                    label="Password"
                    variant="outlined"
                    onChange={(event) => setPassword(event.target.value)}
                    className="wrapperLogIn_password"
                    error={!!errorLogIn}
                />
            </div>
            <Button type="submit">Submit</Button>
        </form>
    );
};

export default LogIn;
