import { NewsAction, NewsActionsTypes, NewsState } from "../../types/news";

const initialState: NewsState = {
    news: [],
    loading: false,
    error: false,
}

export const newsReducer = (state = initialState, action: NewsAction): NewsState => {
    switch (action.type) {
        case NewsActionsTypes.FETCH_NEWS:
            return {...state, loading: true}
        case NewsActionsTypes.FETCH_NEWS_ERROR:
            return {...state, loading: false, error: true}
        case NewsActionsTypes.FETCH_NEWS_SUCCESS:
            return {...state, loading: false, news: action.payload}
        case NewsActionsTypes.DELETE_ITEM_NEWS:
            return {...state, loading: false, news: action.payload}
        default:
            return state;
    }
}